import interfaces from '../../utils/interfaces.js';
var common = require('../../utils/common.js');
import app from "../../App.vue";
export default {
	data() {
		return {
			master_info:"",
			master:'',
			tab:1,
			yulan: "https://www.lianhexiu.com.cn/static/api/images/master/lianhexiu.jpg",
		}
	},
	onShow() {
		this.master_info = app.globalData.master_info;
		if(app.isAuth()){
			this.initData();
		}
	},
	methods: {
		initData() {
			//请求异步ajax获取收入等参数
			this.request({
				url: interfaces.masterInfo,
				header: {
					token:this.master_info.token
				},
				method: 'POST',
				success: res => {
					console.log(res);
					if (res.code == 200) {
						this.master=res.data;
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			});
		},
		changeTab(tab){
			this.tab=tab;
		},
		//更换头像
		changeThumb(){
			var _this=this;
			wx.showModal({
				title: '提示',
				content: '更换头像？',
				success: function(res) {
					if (res.confirm) {
						wx.chooseImage({
							count: 1,
							sizeType: ["compressed"],
							sourceType: ["album", "camera"],
							success: function(e) {
								e.tempFilePaths;
								var t = e.tempFilePaths.length;
								_this.uploadDIY(e.tempFilePaths, 0, 0, 0, t);
							}
						});
					}
				}
			})
		},
		//参数一 e 数据源
		//参数二 t 成功success的数量
		//参数三 a 失败fail的数量
		//参数四 i 完成comlete的数量
		//参数五 n 要上传的图片数量
		//参数六 type 要上传哪个图片（身份证正面、反面或技能证书）
		uploadDIY: function(e, t, a, i, n) {
			var o = this,
				s = this;
			wx.uploadFile({
				url: interfaces.masterUploadThumb,
				filePath: e[i],
				name: "file",
				header: {
					'content-type': 'application/json',
					token: s.master_info.token
				},
				formData: {
					//token: s.master_info.token
				},
				success: function(e) {
					if(e.data){
						e.data=JSON.parse(e.data);
						t++;
						s.master.thumb = e.data;
					}else{
						wx.showToast({
							title: '上传头像失败',
							icon: "none",
						});
					}
				},
				fail: function(e) {
					a++;
				},
				complete: function() {
					++i == n ? wx.hideLoading() : o.uploadDIY(e, t, a, i, n);
				}
			});
		},
		tip() {
			wx.showToast({
				title: '功能未开放，敬请期待',
				icon: "none",
			});
		},

		//下载工单图片
		download() {
			wx.downloadFile({
				url: this.yulan,
				success: function(res) {
					//console.log(res);
					//图片保存到本地
					wx.saveImageToPhotosAlbum({
						filePath: res.tempFilePath,
						success: function(data) {
							wx.showToast({
								title: '保存成功',
								icon: 'success',
								duration: 2000
							})
						},
						fail: function(err) {
							//console.log(err);
							if (err.errMsg === "saveImageToPhotosAlbum:fail auth deny") {
								wx.showModal({
									title: '提示',
									content: '只有授权成功才能使用此功能，是否手动开启授权？',
									success: (res) => {
										if (res.confirm) {
											wx.openSetting({
												success(settingdata) {
													console.log(settingdata)
													if (settingdata.authSetting['scope.writePhotosAlbum']) {
														wx.showToast({
															title: '授权成功',
															icon: 'none',
															mask: true,
														})
													} else {
														wx.showToast({
															title: '授权失败',
															icon: 'none',
															mask: true,
														})
													}
												}
											})
										}
									}
								})
							}
						},
						complete(res) {
							console.log(res);
						}
					})
				}
			})
		},
		
		//跳转分享码页面
		goCode(){
			wx.navigateTo({
				url: 'qrcode',
			})
		},
		//跳转我的团队页面
		goGroup(){
			wx.navigateTo({
				url: 'group',
			})
		},
		// 显示遮罩层
		// 下单工单
		showModal(url) {
			this.showModalStatus= true;
			this.yulan= url;
		},
		
		//跳转我的团队页面
		goStudy(){
			wx.navigateTo({
				url: '../study/article',
			})
		},
		
		//隐藏对话框并保存图片
		hideModal: function() {
			this.download();
			this.showModalStatus= false;
		},

		//隐藏对话框
		closeModal: function() {
			this.showModalStatus= false;
		},
		
		
	}
}
